import {getUserByEmail} from '../../../api/users';
import {enroleMentors} from '../../../api/mentors';
import { useHistory } from 'react-router-dom';

const tabs = [
  {
    value: 'all',
    label: 'Tất cả'
  }
];

const sortOptions = [
  {
    value: 'name|asc',
    label: 'Tên học sinh (tăng)'
  },
  {
    value: 'name|desc',
    label: 'Tên học sinh (giảm)'
  }
];

const applyFilters = (mentors, query, filters) => {
  return mentors;
};

const applyPagination = (mentors, page, limit) => {
  return mentors.slice(page * limit, page * limit + limit);
};

const descendingComparator = (a, b, orderBy) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }

  if (b[orderBy] > a[orderBy]) {
    return 1;
  }

  return 0;
};

const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

const applySort = (mentors, sort) => {
  const [orderBy, order] = sort.split('|');
  const comparator = getComparator(order, orderBy);
  const stabilizedThis = mentors.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);

    if (order !== 0) return order;

    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
};

const useStyles = makeStyles((theme) => ({
  root: {},
  queryField: {
    width: 500
  },
  bulkOperations: {
    position: 'relative'
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: 'absolute',
    width: '100%',
    zIndex: 2,
    backgroundColor: theme.palette.background.default
  },
  bulkAction: {
    marginLeft: theme.spacing(2)
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  }
}));

const MentorCreateForm = ({
  className,
  ...rest
}) => {
  
  const classes = useStyles();
  const history = useHistory();
  
  const isMountedRef = useIsMountedRef();
  const getClasses = useCallback(async () => {
    try {

      if (isMountedRef.current) {

        console.log("class loaded")

      }
    } catch (err) {
      console.error(err);
    }
  }, [isMountedRef]);
  const [mentors, setMentors] = useState([]);
  const [currentTab, setCurrentTab] = useState('all');
  const [selectedMentors, setSelectedMentors] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState('');
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    hasAcceptedMarketing: null,
    isProspect: null,
    isReturning: null
  });

  const searchUser = async() => {
    let getmentors = await getUserByEmail(query);
    setMentors(getmentors)
    console.log(mentors)
  }
  const enroleMentor =  async() => {

    enroleMentors(selectedMentors)
    history.push('/hbon/management/mentors');
  }
  const handleTabsChange = (event, value) => {
    const updatedFilters = {
      ...filters,
      hasAcceptedMarketing: null,
      isProspect: null,
      isReturning: null
    };

    if (value !== 'all') {
      updatedFilters[value] = true;
    }

    setFilters(updatedFilters);
    setSelectedMentors([]);
    setCurrentTab(value);
  };

  const handleQueryChange = (event) => {
    event.persist();
    setQuery(event.target.value);
  };
  const handleSortChange = (event) => {
    event.persist();
    setSort(event.target.value);
  };

  const handleSelectAllMentors = (event) => {
    setSelectedMentors(event.target.checked
      ? mentors.map((mentor) => mentor.id)
      : []);
  };

  const handleSelectOneMentor = (event, mentorId) => {
    if (!selectedMentors.includes(mentorId)) {
      setSelectedMentors((prevSelected) => [...prevSelected, mentorId]);
    } else {
      setSelectedMentors((prevSelected) => prevSelected.filter((id) => id !== mentorId));
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value));
  };

  const filteredClasses = applyFilters(mentors, query, filters);
  const sortedClasses = applySort(filteredClasses, sort);
  const paginatedMentors = applyPagination(sortedClasses, page, limit);
  const enableBulkOperations = selectedMentors.length > 0;
  const selectedSomeMentors = selectedMentors.length > 0 && selectedMentors.length < mentors.length;
  const selectedAllMentors = selectedMentors.length === mentors.length;
  

  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Tabs
        onChange={handleTabsChange}
        scrollButtons="auto"
        textColor="secondary"
        value={currentTab}
        variant="scrollable"
      >
        {tabs.map((tab) => (
          <Tab
            key={tab.value}
            value={tab.value}
            label={tab.label}
          />
        ))}
      </Tabs>
      <Divider />
      <Box
        p={2}
        minHeight={56}
        display="flex"
        alignItems="center"
      >
        <TextField
          className={classes.queryField}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SvgIcon
                  fontSize="small"
                  color="action"
                >
                  <SearchIcon />
                </SvgIcon>
              </InputAdornment>
            )
          }}

          placeholder="Tìm kiếm mentor theo email"
          value={query}
          variant="outlined"
          onChange={handleQueryChange}
        />

        <Box flexGrow={1} />
        <Button
                  variant="contained"
                  color="secondary"
                  onClick={searchUser}
                >
                  Search
                </Button>
      </Box>
      {enableBulkOperations && (
        <div className={classes.bulkOperations}>
          <div className={classes.bulkActions}>
            <Checkbox
              checked={selectedAllMentors}
              indeterminate={selectedSomeMentors}
              onChange={handleSelectAllMentors}
            />
            <Button
              variant="outlined"
              className={classes.bulkAction}
              onClick={enroleMentor}
            >
              Set Mentor
            </Button>
          </div>
        </div>
      )}
      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                </TableCell>
                <TableCell>
                  firstname
                </TableCell>
                <TableCell>
                  lastname
                </TableCell>
                <TableCell>
                  role
                </TableCell>
                <TableCell>
                  email
                </TableCell>
                <TableCell>
                  uid
                </TableCell>
                <TableCell>
                moodleid
                </TableCell>
                <TableCell align="right">
                  Actions
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedMentors.map((mentor) => {
                let upvotes = null;
                let downvotes = null;
                if("reactions" in mentor){
                  upvotes = mentor.reactions.upvotes
                  downvotes = mentor.reactions.downvotes  
                }
                
                const isMentorSelected = selectedMentors.includes(mentor.id);
                
                if(!("tags" in mentor)){
                  mentor.tags=[""];
                }
                return (
                  <TableRow
                    hover
                    key={mentor.id}
                    selected={isMentorSelected}
                  >
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={isMentorSelected}
                        onChange={(event) => handleSelectOneMentor(event, mentor.id)}
                        value={isMentorSelected}
                      />
                    </TableCell>
                    <TableCell>
                      <Box
                        display="flex"
                        alignItems="center"
                      >
                            {mentor.firstname}
                      </Box>
                    </TableCell>
                    <TableCell>
                            {mentor.lastname}
                    </TableCell>
                    <TableCell>
                            {mentor.role}
                    </TableCell>
                    <TableCell>
                            {mentor.email}
                    </TableCell>
                    <TableCell>
                    {mentor.id}
                    </TableCell>
                    <TableCell>
                    {mentor.moodleUserId}
                    </TableCell>
                    <TableCell align="right">
                      <IconButton
                  onClick={enroleMentor}
                      >
                        <SvgIcon fontSize="small">
                          <EditIcon />
                        </SvgIcon>
                      </IconButton>
                      
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredClasses.length}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

MentorCreateForm.propTypes = {
  className: PropTypes.string,
};

MentorCreateForm.defaultProps = {
  
};

export default MentorCreateForm;
